.card{
    width: 180px;
    height: 300px;
    margin: 15px;
    padding: 10px 5px;
    background-color: rgba(0,0,0,0.1);
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 15px rgba(0,0,0,0.3);
}

.img{
    max-width: 100%;
    max-height: 70%;
    background-color: red;
    border-radius: 20px;
}

.text{
    max-height: 20%;
}

.starbar{
    display: flex;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 10%;
    /* background-color: white; */
}

.star_yellow{
    
    color: yellow;
}

.star_gray{
    color: gray;
}